<template>
  <div class="container">
    <div class="main__cars" v-if="todos">
      <div class="main__cars-item" v-for="user in todos" :key="user.id">
        <p>UserId:1</p>
        <p>Id: {{ user.id }}</p>
        <p>Title: {{ user.title }}</p>
        <p>Completed: {{ user.completed }}</p>
      </div>
    </div>
    <div v-else>Идет загрузка...</div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  computed: {
    todos() {
      return this.$store.state.todos;
    },
  },

  methods: {
    getTodos() {
      this.$store.dispatch("getTodos");
    }
  },
  created() {
    this.getTodos();
  },
};
</script>
